/*
*地图组件
*/
<template xmlns:v-bind="http://www.w3.org/1999/xhtml">

  <div class='baseMapWrapper'>
    <div id="map"></div>
    <div style="position: absolute;bottom: 10px;left:45px;color:blue;font-size: 16px" id="coords"></div>

    <!--选中地图点的浮云-->
    <div id="pointPopup" class="pointPopup" ref="pointPopup">
      <div class="inner flex_col">
        <div class="close" @click.stop="closePop">
          <Icon type="md-close" size="20"/>
        </div>

        <div class="coords">
          <p class="label">当前位置：</p>
          <p>经度：{{pointCoords[0]}}</p>
          <p>纬度：{{pointCoords[1]}}</p>
        </div>
        <div class="search">
          <Button type="primary" shape="circle" icon="ios-search" size="small" @click.stop='search'>搜索</Button>
        </div>

      <div class="arrow"></div>

    </div>
    </div>

    <!--加载中-->
    <!--<Spin fix size="large" v-if="showLoading" style="background-color: rgba(255,255,255,.35);"></Spin>-->
  </div>


</template>


<script>

  import {InitOlVecMap, InitMapboxMap} from '../js/render.js'
  import { mapGetters,mapActions } from 'vuex'
  // import mapboxgl from 'mapbox-gl'
  import pos_cursor from '../../static/images/map/red_cursor.png'  //地图定位的图标
  import num_marker from '../../static/images/map/result_markers.png'  //地图定位的图标（带序号）
  import num_marker_high from '../../static/images/map/result_markers_sel.png'  //地图定位的图标（带序号）

  export default {
    name: 'baseMap',
    data() {
      return {
        showLoading:false,
        pointCoords:[],//选中地图点的浮云坐标
      }
    },
    components: {},
    computed: {
      ...mapGetters(['canSelPoint','markLngLat']) ,// 动态计算属性
    },
    props: {
    },
    created() {
    },

    mounted() {
      this.initMap();
    },
    methods: {
      ...mapActions( //mapActions需要写在methods里面
        ['updateSearchPointCoordArr']),// 取得这些方法
      //地图初始化
      initMap() {
        var $this = this;

        // $this.showLoading=true;
        //初始化房屋面
        InitOlVecMap(function (map) {

          var overlay = new ol.Overlay({
            // id:'overlay',
            element: document.getElementById('pointPopup'),
            position:[0,0],
            positioning: 'center-center',
            insertFirst:false
          });
          map.addOverlay(overlay);
          map.on('click',function (e) {
            if($this.canSelPoint){
              var point = e.coordinate;
              overlay.setPosition(point);
              $this.pointCoords = point
            }

          });
          var mousePositionControl = new ol.control.MousePosition({
            className: 'custom-mouse-position',
            target: document.getElementById('coords'),
            undefinedHTML: ' '
          });
          map.addControl(mousePositionControl);
          $this.map = map;
          $this.overlay = overlay;
        })
      },
      //关闭浮云
      closePop(){
        this.overlay.setPosition([0,0]);
        this.pointCoords=[]
      },
      //搜索坐标对应的位置
      search(){
        this.updateSearchPointCoordArr(this.pointCoords);
      }

    },
    beforedestroy() {
      if (this.baseMap) {
        this.baseMap.remove();
      }
    },
    watch: {
      //监听mark坐标变化
      markLngLat: {
        handler: function (val, oldVal) {
          let map = this.map;
          if (val && val != null) {
            if (val[0] == 'high') {
              if (vectorSource.idIndex_['high']) {
                //先清除原来的高亮
                map.removeMarker(vectorSource.idIndex_['high']);
              }
              //更新高亮图层
              let id = val[1];//id,即feature的name
              if (vectorSource.idIndex_[id]) {
                let lngLat = vectorSource.idIndex_[id]['values_']['lngLat'];
                let i = parseInt(vectorSource.idIndex_[id]['values_']['index']);
                let markPoint = new ol.Feature({
                  geometry: new ol.geom.Point(lngLat),
                });
                markPoint.setStyle(new ol.style.Style({
                  image: new ol.style.Icon(({
                    src: num_marker_high,
                    offset: [21 * i, 0],
                    size: [21, 31]
                  })),
                }));
                vectorSource.idIndex_['high'] = markPoint;
                map.addMarker(markPoint);
                //定位
                map.setCenter(lngLat[0], lngLat[1]);
                map.setLevel(map.markLevel);

              } else {
                this.$Message.warning('没有查询到坐标！')
              }

            } else if (typeof val[0] == 'object') {
              //清除所有marker
              map.removeAllMarker();

              let sum_x = 0;
              let sum_y = 0;//记录坐标总值，用于计算中心点
              let ll_length = 0;//有效坐标个数
              //非高亮
              for (var i in val) {
                if (val[i]) {
                  //图片显示
                  let markPoint = new ol.Feature({
                    geometry: new ol.geom.Point([val[i][0], val[i][1]]),
                  });
                  if (val[i][2]) {
                    vectorSource.idIndex_[val[i][2].toString()] = markPoint
                  }
                  markPoint.setProperties({
                    lngLat: [val[i][0], val[i][1]],
                    index: i
                  });

                  markPoint.setStyle(new ol.style.Style({
                    image: new ol.style.Icon(({
                      src: num_marker,
                      offset: [21 * i, 0],
                      size: [21, 31]
                    })),
                  }));

                  sum_x += val[i][0];
                  sum_y += val[i][1];
                  ll_length += 1;
                  map.addMarker(markPoint);
                }
              }
              if (ll_length) {
                map.setCenter(sum_x / ll_length, sum_y / ll_length);
                map.setLevel(12);
              } else {
                map.setCenter(map.mapCenter[0], map.mapCenter[1]);
                map.setLevel(map.mapLevel);
              }
            } else {
              //清除所有marker
              map.removeAllMarker();

              if(val[0]&&val[1]){
                let markPoint = new RPointMarker(val[0], val[1], pos_cursor, 22);
                map.addMarker(markPoint);
                map.setCenter(val[0], val[1]);
                map.setLevel(map.markLevel);
              }else{
                map.setCenter(map.mapCenter[0], map.mapCenter[1]);
                map.setLevel(map.mapLevel);
              }

            }


            // this.baseMap.flyTo({
            //   center: val,
            //   zoom:16
            // });//设置地图中心
            // this.loc_marker.setLngLat(val);//设置点坐标
          } else {
            map.removeAllMarker();
            // this.loc_marker.setLngLat([0,0]);//设置点坐标
            map.setCenter(map.mapCenter[0], map.mapCenter[1]);
            map.setLevel(map.mapLevel);
          }

        },
        immediate: false
      }
    }
  }
</script>

<style scoped>

  .baseMapWrapper {
    text-align: left;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    z-index: 100;
  }

  #map {
    width: 100%;
    height: 100%;
    display: block;
  }
  .pointPopup{
    /*position: relative;*/
  }
  .pointPopup .inner{
    position: absolute;
    top: -118px;
    left: -90px;
    box-shadow: 1px 2px 1px rgba(0,0,0,.15);
    border-radius: 3px;
    width: 180px;
    height: 110px;
    background: #fff;
    padding: 10px;
  }
  .pointPopup .close{
    align-self: flex-end;
    flex-shrink: 0;
  }
  .pointPopup .coords{
    height: 100%;
    align-self: flex-start;
    width: 100%;
  }
  .pointPopup .coords .label{
       font-weight: bold;
     }
  .pointPopup .search{
    align-self: flex-end;
    flex-shrink: 0;
  }
  .pointPopup .arrow{
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 8px 6px 0;
    border-color: #fff transparent transparent;
    position: absolute;
    bottom: -7px;
    left: 48%;
  }
</style>



